@use '../foundation/_variable' as *;

.l-header {
    width: 18rem;
    top: 0;
    z-index: 999;
    background-color: $color-base;
    height: 8rem;
    padding: 1.6rem 0 1rem 1.4rem;
    position: relative;
    &__content {
        text-align: center;
        align-items: center;
        height: 100%;
    }
    &__logo {
        width: 15.3rem;
        height: 2.45rem;
        margin: 0 auto;
        padding: 1.5rem 0;
    }
}