@use '../../foundation/_variable' as *;

.p-reason {
    padding: 7.2rem 0 5rem;
    margin: 0 auto;
    background: $color-bg-yellow;
    &__wrapper {
        margin-top: 3.2rem;
    }
    &__list {
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
    }
    &__listCol {
        display: flex;
        gap: 1.8rem;
        margin-top: 1.8rem;  
        flex-wrap: wrap;  
        li {
            width: 29rem;
        }    
    }
    &__note {
        margin-top: 2rem;
        font-size: 1.5rem;
        line-height: 1.3;
        font-weight: bold;
    }
}