@use '../../foundation/_variable' as *;

.p-faq {
    padding: 6rem 0 6.8rem;
    &__title {
        text-align: center;
        margin-top: 5.6rem;
        font-size: 4.4rem;
        font-weight: bold;
        line-height: 1.1;
        @include breakpoint(small) {
            font-size: 8vw;
        }
    }
    &__accordion {
        margin-top: 5rem;
        dl {
            :first-child {
                margin-top: 0;
            }
        }
        dt {
            font-size: 2.1rem;
            min-height: 5.5rem;
            border: 1px solid $color-font;
            border-radius: 10px 0 0 0;
            display: flex;
            align-items: center;
            user-select: none;
            cursor: pointer;
            margin-top: 1.4rem;
            padding: 1.6rem 5.5rem 1.6rem 3rem;
            display: flex;
            position: relative;
            @include breakpoint(small) {
                font-size: 3.5vw;
            }
            ::after {
                content: "";
                display: block; 
                width: 1.6rem;
                height: 1.6rem;
                border-left: 2.5px solid $color-font;
                border-bottom: 2.5px solid $color-font;
                transform: rotate(-45deg) translateY(-50%);
                position: absolute;
                right: 2.4rem;
                top: 50%;
            }
            &.is-active {
                & span::after {
                    transform: rotate(135deg);
                }
                + dd {
                    visibility: inherit;
                    transition: visibility .2s, max-height .1s;
                    padding: 1rem;
                }
            }
        }
        & dd {
            color: $color-font;
            visibility: hidden;
            max-height: 0;
            border: none;
        }
    }
    &__number {
        font-family: $font-din;
        margin-right: 2rem;
    }
}