*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  line-height: 1.5;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  color: #663300;
  font-size: 1.6rem;
  font-family: "Noto Sans JP", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", "DIN Alternate", "Kozuka Mincho Pro", sans-serif;
  min-width: 350px;
  margin: 0 auto;
  width: 100%;
  overflow-x: hidden;
  max-width: 640px;
}

img,
video,
object {
  max-width: 100%;
  height: auto;
  border: none;
  display: block;
}

a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

a:hover img {
  text-decoration: none;
  opacity: 0.8;
  transition: all 0.3s;
}

li {
  list-style: none;
}

.l-header {
  width: 18rem;
  top: 0;
  z-index: 999;
  background-color: #ffffff;
  height: 8rem;
  padding: 1.6rem 0 1rem 1.4rem;
  position: relative;
}
.l-header__content {
  text-align: center;
  align-items: center;
  height: 100%;
}
.l-header__logo {
  width: 15.3rem;
  height: 2.45rem;
  margin: 0 auto;
  padding: 1.5rem 0;
}

.l-footer {
  background-color: #ffffff;
}
.l-footer__content {
  display: flex;
  padding: 0 2rem;
  gap: 2rem;
  align-items: flex-end;
  justify-content: space-between;
}
@media (max-width: 576px) {
  .l-footer__content {
    gap: 0.5rem;
    padding: 0 1rem;
  }
}
.l-footer__logo {
  flex-basis: 20%;
  height: auto;
  margin-bottom: 1.4rem;
}
.l-footer__img {
  flex-basis: 50%;
  height: auto;
}
.l-footer__nav ul {
  flex-basis: 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  margin-bottom: 2rem;
  font-size: 1.2rem;
}
.l-footer__nav ul li {
  text-align: left;
}
@media (max-width: 576px) {
  .l-footer__nav ul {
    font-size: 1.1rem;
  }
}
.l-footer small {
  background-color: #009933;
  font-weight: normal;
  font-size: 1.3rem;
  color: #ffffff;
  text-align: center;
  height: 4rem;
  display: block;
  line-height: 4rem;
}

.c-inner {
  max-width: 640px;
  margin: 0 auto;
  padding: 0 2rem;
}

.c-btn {
  display: block;
  width: 52rem;
  max-width: 52rem;
  height: 12rem;
  font-size: 3.6rem;
  font-weight: bold;
  color: #ffffff;
  background: #009933;
  padding: 1rem 2rem;
  border-radius: 6rem;
  box-shadow: 0px 3px 6px gray;
  text-align: center;
  margin: 0 auto;
  letter-spacing: -0.1em;
}
.c-btn:hover {
  opacity: 0.8;
  transition: all 0.3s;
}
@media (max-width: 576px) {
  .c-btn {
    font-size: 6vw;
    height: 20vw;
    width: 86vw;
  }
}

.c-sectionTitle {
  margin: 0 auto;
  text-align: center;
}
.c-sectionTitle__en {
  font-size: 2.1rem;
  font-family: "DIN Alternate", "Noto Sans JP", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  line-height: 1.2;
  font-weight: bold;
  text-transform: uppercase;
}
.c-sectionTitle__jp {
  font-size: 6rem;
  font-weight: bold;
  display: inline-block;
  border-top: 1px solid #663300;
}

.p-fv {
  width: 100%;
  position: absolute;
  top: 0;
  display: block;
}
.p-fv__height {
  margin-top: 78rem;
}

.p-cta {
  padding-top: 3rem;
  background: linear-gradient(#fdf7ed, transparent 20%);
}
.p-cta__bgBase {
  background: #ffffff;
}
.p-cta__bgYellow {
  background: linear-gradient(#ffffcc, transparent 20%);
}
.p-cta__heading img {
  margin: 0 auto;
  width: 27rem;
  height: 5rem;
}
.p-cta__btn {
  margin: 2rem auto;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "DIN Alternate", "Zen Kaku Gothic Antique", "Arial", "Yu Gothic", "Meiryo", sans-serif;
}
.p-cta__btn::after {
  content: "";
  display: block;
  width: 3.4rem;
  height: 2.8rem;
  background: url("../../assets/img/btn-arrow.svg") center/contain no-repeat;
  margin-left: 2.4rem;
}
@media (max-width: 576px) {
  .p-cta__btn {
    justify-content: space-between;
  }
  .p-cta__btn::after {
    margin-left: 0;
    width: 5.6vw;
  }
}
.p-cta__img img {
  width: 100%;
  height: 20rem;
  object-fit: cover;
  object-position: bottom;
}
.p-cta__img__short img {
  height: 15.7rem;
}

.p-campaign {
  background-color: #fdf7ed;
  padding-bottom: 3rem;
}
.p-campaign__title {
  width: 28rem;
  text-align: center;
  margin: 0 auto;
  padding-top: 4rem;
  font-size: 2.4rem;
}
.p-campaign__titleJp {
  border-bottom: 2px solid #663300;
  font-weight: bold;
  font-family: "DIN Alternate", "Zen Kaku Gothic Antique", "Arial", "Yu Gothic", "Meiryo", sans-serif;
}
.p-campaign__titleEn {
  text-transform: uppercase;
  font-family: "DIN Alternate", "Noto Sans JP", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  letter-spacing: 0.1em;
  line-height: 1.1;
}
.p-campaign__heading {
  margin: 2.8rem auto 0;
  color: #ffffff;
  font-weight: bold;
  font-size: 3.6rem;
  max-width: 46.6rem;
  height: 11rem;
  letter-spacing: -0.1em;
  line-height: 1.2;
  background: url("../../assets/img/campaign-heading-bg@2x-8.png");
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 576px) {
  .p-campaign__heading {
    font-size: 6vw;
  }
}
.p-campaign__catch {
  margin: 2.8rem auto 0;
  max-width: 46.6rem;
  height: 6rem;
}
.p-campaign__content {
  margin-top: 3.3rem;
  letter-spacing: -0.05em;
  line-height: 1.36;
  text-align: center;
  font-size: 3rem;
  font-weight: bold;
}
@media (max-width: 576px) {
  .p-campaign__content {
    font-size: 5vw;
  }
}
.p-campaign__chart {
  margin: 2.3rem auto 0;
}
.p-campaign__note {
  font-size: 1.6rem;
  margin-top: 2.3rem;
  color: #231815;
  font-weight: medium;
  line-height: 1.5;
  letter-spacing: 0.01em;
  text-align: left;
}
.p-campaign__note :last-child {
  margin-top: 2.4rem;
}
@media (max-width: 576px) {
  .p-campaign__note {
    font-size: 1.1rem;
  }
}
.p-campaign__list {
  margin-top: 2.6rem;
}
.p-campaign__listItem {
  margin: 0 auto 3.5rem;
  width: 84%;
  height: 53rem;
  object-fit: cover;
  object-position: top;
}
.p-campaign__listItem img {
  width: 100%;
}

.p-reason {
  padding: 7.2rem 0 5rem;
  margin: 0 auto;
  background: #ffffcc;
}
.p-reason__wrapper {
  margin-top: 3.2rem;
}
.p-reason__list {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.p-reason__listCol {
  display: flex;
  gap: 1.8rem;
  margin-top: 1.8rem;
  flex-wrap: wrap;
}
.p-reason__listCol li {
  width: 29rem;
}
.p-reason__note {
  margin-top: 2rem;
  font-size: 1.5rem;
  line-height: 1.3;
  font-weight: bold;
}

.p-lineup {
  background-color: #fdf7ed;
  padding-top: 3rem;
}
.p-lineup__gridWrapper {
  margin-top: 4rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  gap: 1.3rem;
}
.p-lineup__gridItem {
  position: relative;
}
.p-lineup__img::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(transparent 60%, rgba(0, 0, 0, 0.7));
}
.p-lineup__name {
  font-size: 2.2rem;
  color: #ffffff;
  font-weight: bold;
  position: absolute;
  left: 0;
  bottom: 0;
  user-select: none;
  margin: 0 0 6px 4px;
}
@media (max-width: 576px) {
  .p-lineup__name {
    font-size: 3.6vw;
  }
}

.p-voices {
  padding: 9rem 0 8.8rem;
  background: #fdf7ed;
}
.p-voices__cardUnit {
  margin-top: 8.6rem;
  font-family: "Helvetica", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.p-voices__card {
  padding: 2.6rem 1.8rem 2.6rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ffffff;
}
@media (max-width: 576px) {
  .p-voices__card {
    flex-direction: column;
    align-items: flex-start;
    gap: 1.6rem;
    padding: 1.6rem;
  }
}
.p-voices__cardHeading {
  flex-basis: 50%;
  border-left: 15px solid #663300;
  padding-left: 0.6rem;
  font-size: 2.4rem;
  line-height: 1.2;
  font-weight: bold;
}
.p-voices__cardHeading p {
  height: 6.2rem;
}
.p-voices__cardtext {
  flex-basis: 50%;
  font-size: 1.8rem;
  color: #2F3232;
}
.p-voices__content {
  flex-basis: 50%;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.p-voices__imgWrapper {
  flex-basis: 50%;
  text-align: center;
  margin: 0 auto;
}
.p-voices__img img {
  margin: 0 auto;
}

.p-compare {
  padding-top: 6.2rem;
  margin: 0 auto;
}

.p-shopping {
  padding: 8rem 0;
  background: #ffffcc;
}
.p-shopping__intro {
  margin-top: 2.4rem;
  text-align: center;
  font-weight: bold;
  font-size: 2.1rem;
  line-height: 1.14;
}
.p-shopping__content {
  margin-top: 6.5rem;
}

.p-faq {
  padding: 6rem 0 6.8rem;
}
.p-faq__title {
  text-align: center;
  margin-top: 5.6rem;
  font-size: 4.4rem;
  font-weight: bold;
  line-height: 1.1;
}
@media (max-width: 576px) {
  .p-faq__title {
    font-size: 8vw;
  }
}
.p-faq__accordion {
  margin-top: 5rem;
}
.p-faq__accordion dl :first-child {
  margin-top: 0;
}
.p-faq__accordion dt {
  font-size: 2.1rem;
  min-height: 5.5rem;
  border: 1px solid #663300;
  border-radius: 10px 0 0 0;
  display: flex;
  align-items: center;
  user-select: none;
  cursor: pointer;
  margin-top: 1.4rem;
  padding: 1.6rem 5.5rem 1.6rem 3rem;
  display: flex;
  position: relative;
}
@media (max-width: 576px) {
  .p-faq__accordion dt {
    font-size: 3.5vw;
  }
}
.p-faq__accordion dt ::after {
  content: "";
  display: block;
  width: 1.6rem;
  height: 1.6rem;
  border-left: 2.5px solid #663300;
  border-bottom: 2.5px solid #663300;
  transform: rotate(-45deg) translateY(-50%);
  position: absolute;
  right: 2.4rem;
  top: 50%;
}
.p-faq__accordion dt.is-active span::after {
  transform: rotate(135deg);
}
.p-faq__accordion dt.is-active + dd {
  visibility: inherit;
  transition: visibility 0.2s, max-height 0.1s;
  padding: 1rem;
}
.p-faq__accordion dd {
  color: #663300;
  visibility: hidden;
  max-height: 0;
  border: none;
}
.p-faq__number {
  font-family: "DIN Alternate", "Noto Sans JP", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  margin-right: 2rem;
}

.p-contact {
  padding: 8.2rem 0 6.8rem;
  text-align: center;
}
.p-contact__text {
  font-size: 2.4rem;
}
@media (max-width: 576px) {
  .p-contact__text {
    font-size: 4vw;
  }
}
.p-contact__phone {
  font-size: 5.8rem;
  line-height: 1;
  margin-top: 2.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "DIN Alternate", "Noto Sans JP", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
}
.p-contact__phone::before {
  content: "";
  display: block;
  width: 5.6rem;
  height: 5.6rem;
  background: url("../../assets/img/contact-phone.svg") center/contain no-repeat;
  margin-right: 1rem;
}
@media (max-width: 576px) {
  .p-contact__phone {
    font-size: max(4rem, 9.6vw);
  }
}
.p-contact__detail {
  font-size: 1.9rem;
  font-family: "Helvetica", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  margin-top: 1.4rem;
}
@media (max-width: 576px) {
  .p-contact__detail {
    font-size: 4vw;
  }
}
.p-contact__btn {
  margin-top: 4rem;
  background: #ff9900;
}

