@use '../../foundation/_variable' as *;

.c-sectionTitle {
    margin: 0 auto;
    text-align: center;
    &__en {
        font-size: 2.1rem;
        font-family: $font-din;
        line-height: 1.2;
        font-weight: bold;
        text-transform: uppercase;
    }
    &__jp {
        font-size: 6rem;
        font-weight: bold;
        display: inline-block;
        border-top: 1px solid $color-font;
    }
}