@use '../../foundation/_variable' as *;

.p-lineup {
    background-color: $color-bg-cream;
    padding-top: 3rem;
    &__gridWrapper {
        margin-top: 4rem;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
        gap: 1.3rem;
    }
    &__gridItem {
        position: relative;
    }
    &__img::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(transparent 60%,  rgba(0, 0, 0, .7));
    }
    &__name {
        font-size: 2.2rem;
        color: $color-base;
        font-weight: bold;
        position: absolute;
        left: 0;
        bottom: 0;
        user-select: none;
        margin: 0 0 6px 4px;
        @include breakpoint(small) {
            font-size: 3.6vw;
        }
    }
}