@use '../foundation/_variable' as *;

.l-footer {
    background-color: $color-base;
    &__content {
        display: flex;
        padding: 0 2rem;
        gap: 2rem;
        align-items: flex-end;
        justify-content: space-between;
        @include breakpoint(small) {
            gap: .5rem;
            padding: 0 1rem;
        }
    }
    &__logo {
        flex-basis: 20%;
        height: auto;
        margin-bottom: 1.4rem;
    }
    &__img {
        flex-basis: 50%;
        height: auto;
    }
    &__nav ul {
        flex-basis: 30%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        margin-bottom: 2rem;
        font-size: 1.2rem;
        li {
            text-align: left;
        }
        @include breakpoint(small) {
            font-size: 1.1rem;
        }
    }
    small {
        background-color: $color-green;
        font-weight: normal;
        font-size: 1.3rem;
        color: $color-base;
        text-align: center;
        height: 4rem;
        display: block;
        line-height: 4rem;
    }
}